import React, { Component } from 'react'
import Nav from './NavBar'

export default class Explore extends Component {
   constructor(){
       super();
this.state={
    height:'',
    weight:'',
    bmiIndex:''
}  
 
this.submitHandler=this.submitHandler.bind(this);
this.handleHeight=this.handleHeight.bind(this);
this.handleWeight=this.handleHeight.bind(this);

}

handleHeight(e) {
        this.setState({[e.target.name]:[e.target.value]});
      }
handleWeight(e){
   this.setState({[e.target.name]:[e.target.value]}) 
}
submitHandler=(e)=>{
    let height=(this.state.height);
    let weight=(this.state.weight);
    let bmi=(weight)/((height/100)*(height/100));
    bmi= Math.floor(bmi * 100) / 100;
    this.setState({bmiIndex:bmi.toFixed(2)})
    if(bmi>25){
        this.setState({bmiIndex:bmi.toString()+' (Overweight)'})
    }
    else if(bmi<18.5){
        this.setState({bmiIndex:bmi.toString()+' (Underweight)'})  
    }
  
    else if(bmi>=18.5 && bmi<=25)
    this.setState({bmiIndex:bmi.toString()+' (Normal)'})
console.log(bmi);
  if(this.state.height==''|| this.state.weight=='')
       alert('fields cannot be empty')
      
}

    render() {
        return (
            <div>
                <Nav></Nav>
               <div className="container">
                  
                  <form ><br/>
                  <h3 className="container left">Calculate BMI</h3> 
                   <div className="container left">
                   <br/><br/> 
                   <label htmlFor="height">Height</label>
                   <input type="number" name="height" placeholder="in centimeters"  onChange={this.handleHeight}/>
                   </div>
                  
                   <div className="container left">
                   <label htmlFor="weight">Weight</label>
                   <input type="number" name="weight" placeholder="kilograms" onChange={this.handleWeight}/>
                   </div>
                   <div className="container left">
                       <input type="button" className="btn red darken-2 pulse" onClick={()=>{this.submitHandler()}} value="Calculate"/>
                  <input type="text" name="endIndex" placeholder="Your B.M.I is..." value={this.state.bmiIndex}/>
                   </div>
                   </form>

               </div>
            </div>
        )
    }
}
